import {
  Form,
  InputGroup,
  FormControl,
  Button,
  Container,
  Row,
  Col,
} from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faLock,
  faPiggyBank,
  faHandHoldingUsd,
  faArrowRight,
  faSearch,
} from "@fortawesome/free-solid-svg-icons";
import React from "react";
import CustomDivider from "./CustomDivider";

function Main() {
  const [search, setSearch] = React.useState("");
  const doSearch = async (e) => {
    e.preventDefault();
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();

    if (search.trim() !== "") {
      window.location.assign(`/explore/search?query=${search.trim()}`);
    }
  };

  return (
    <div>
      <header className="section-a" id="home">
        <div className="particles-container">
          <Container className="text-center">
            <Row>
              <Col>
                <div className="isometric-holder">
                  <div className="isometric"></div>
                </div>
              </Col>
            </Row>
            <Row>
              <h1 className="mt-5 accent">Dingocoin NFT Platform</h1>
              <p className="mb-4">The next generation of NFTs</p>
            </Row>
            <Row className="d-flex flex-row">
              <Form
                className="mx-auto mt-3 mb-4"
                onSubmit={doSearch}
                style={{ maxWidth: "584px" }}
              >
                <InputGroup>
                  <InputGroup.Text id="basic-addon1">
                    <FontAwesomeIcon icon={faSearch} />
                  </InputGroup.Text>
                  {search !== null && (
                    <FormControl
                      autoFocus={true}
                      className="search-box"
                      placeholder={"Search NFTs, collections, profiles..."}
                      value={search}
                      onChange={(e) => {
                        if (e.target.value.length <= 50) {
                          setSearch(e.target.value);
                        }
                      }}
                    />
                  )}
                  {search !== "" && (
                    <Button variant="primary" type="submit">
                      <FontAwesomeIcon icon={faArrowRight} />
                    </Button>
                  )}
                </InputGroup>
              </Form>
            </Row>
            <div className="mt-4 mb-2">
              <CustomDivider />
            </div>
            <h2>Get started</h2>
            <div className="d-flex flex-row mb-4 mt-4">
              <a href="/explore" className="mt-3 ms-auto me-3">
                <Button
                  className="popup-button px-4 py-2"
                  variant="primary"
                >
                  Explore NFTs
                </Button>
              </a>
              <a href="/create" className="mt-3 ms-3 me-auto">
                <Button
                  className="popup-button px-4 py-2"
                  variant="primary"
                >
                  Create your NFT
                </Button>
              </a>
            </div>
            <Row className="pt-3 pb-2">
              <Col>
                <div className="project-card mx-auto">
                  <div className="logo-holder">
                    <FontAwesomeIcon className="faicon" icon={faPiggyBank} />
                  </div>
                  <h5>Ultra-low Costs</h5>
                  <p>Create and trade NFTs with {"<$0.001"} gas fees</p>
                </div>
              </Col>
              <Col>
                <div className="project-card mx-auto">
                  <div className="logo-holder">
                    <FontAwesomeIcon
                      className="faicon"
                      icon={faHandHoldingUsd}
                    />
                  </div>
                  <h5>On-chain Trading</h5>
                  <p>You receive earnings and royalties immediately</p>
                </div>
              </Col>
              <Col>
                <div className="project-card mx-auto">
                  <div className="logo-holder">
                    <FontAwesomeIcon className="faicon" icon={faLock} />
                  </div>
                  <h5>Cryptographically Unique</h5>
                  <p>The same content can never be reused in another NFT</p>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </header>
    </div>
  );
}

export default Main;
